@use 'base/variables' as v;

.citas{
    list-style: none;
    padding: 0;

    li{
        padding: 1rem;
        border-top: 1px solid v.$blanco;
        p{
            font-weight: v.$bold;
            color: v.$azul;
        }

        span,
        .servicio{
            font-weight: v.$regular;
            color: v.$blanco;
        }
    }
}

.barra-servicios-admin{
    width: 40%;
    margin: 0 auto;
    a{
        text-decoration: none;
    }
    .boton{
        margin: 1rem;
    }
}

.servicios{
    @extend .citas;
    .acciones{
        
        margin: 0;
        a:hover{
            text-decoration: none;
        }
        
        
        .accion-proyecto{
            margin: 1rem;
            position: relative;

            .texto-alerta{
                display: none;
            }
        }
        
        
        .accion-proyecto:hover{
            .texto-alerta{
                position: absolute;
                background-color: rgba(255, 255, 255, 0.7);
                display: block;
                color: v.$negro;
                border-radius: 1rem;
                padding: 0.4rem;
            }
        }
        .accion-proyecto:hover{
            .texto-alerta{
                bottom: 90%;
            }
        }
    }
    .boton,
    .boton-foto,
    .boton-eliminar{
        margin: 0;
    }

}

table.usuarios{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    text-align: center;

    .boton,
    .boton-eliminar{
        width: fit-content;
        margin: 1rem auto;
        padding: 0;
    }

    thead{
        background-color: v.$negro;

        th{
            color: v.$blanco;
            padding: 2rem;
            border: 1px solid v.$gris;
        }
    }

    tbody th{
        border: 1px solid v.$gris;
    }
}

.acciones-cita{
    display: flex;
    justify-content: space-around;

    a{
        all: unset;
    }
    .boton{
        background-color: v.$azul;
        font-weight: 700;
        font-size: 2rem;
    }
    .boton,
    .boton-eliminar{
        margin: 1rem;
        padding: 1rem;
    }
}

a.boton{
    text-decoration: none;
}