@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-resumen{
    display: grid;
    grid-template-columns: 3rem;
    padding-right: 1rem;
    gap: 1rem;

    .textoCita{
        text-align: center;
        grid-column: 1;
        writing-mode:vertical-rl;
        transform:rotate(180deg);
        background-color: violet;
        line-height: 3rem;
        color: black;
    }

    .textoServicios{
        text-align: center;
        grid-column: 1;
        writing-mode:vertical-rl;
        transform:rotate(180deg);
        background-color: violet;
        color: black;
        line-height: 3rem;
    }

    p{
        grid-column: 2;
        margin-bottom: 1rem;
    }

    .boton{
        grid-column: 1 / 3;
    }

    span{
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
    .porcentaje{
        color: v.$blanco;
        background-color: v.$rojo;
        border-radius: 1rem;
        padding: 0.5rem;
        font-size: 1rem;
    }
}

.contenedor-servicio{
    padding: 1rem 0;
    border-bottom: 1px solid v.$gris;

    .oferta{
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2.4rem;

        .primero{
            text-decoration: line-through;
            text-decoration-color: v.$azul;
            text-decoration-thickness: 0.4rem;
            color: v.$blanco;
            font-weight: v.$regular;
        }
        .segundo{
            color: v.$blanco;
            text-decoration: none;
        }
        .tercero{
            color: v.$blanco;
            background-color: v.$rojo;
            border-radius: 1rem;
            padding: 0.5rem;
            font-size: 1rem;
        }
    }
    .sin-oferta{
        span{
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 2.4rem;
        }
    }
}