@use 'base/variables' as v;

.alerta{
    padding: calc(v.$separacion / 4);
    color: v.$blanco;
    width: 90%;
    display: block;
    margin: 0 auto 2rem auto;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;

    &.error{
        background-color: v.$rojo;
    }
    &.exito{
        background-color: v.$verde;
    }
    &.advertencia{
        background-color: #FF9900;
    }
}