@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }
    position: relative;

    .imagen{
        height: 22rem;
        background-image: url('../img/1.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        position: relative;
        @include m.tablet{
            height: auto;
        }
        .datos{
            position: absolute;
            bottom: 0;
            width: 80%;
            left:20%;

            .horarios,
            .direccion{
                width: 60%;
                text-align: center;
                margin-left: 3rem;
            }

            .redes{
            
                display: flex;
                justify-content: space-evenly;
                bottom: 1rem;
                left: 10%;
                width: 70%;
                padding: 1rem;
                background-color: rgba(255, 255, 255, 0.2);
                img{
                    width: 5rem;
                    height: 5rem;
    
                    &:hover{
                        animation: fadeinout 1.5s ease-in-out infinite;
                        border: 3px solid rgba(0, 0, 0, 0.6);
                        border-radius: 1rem;
                        box-sizing: border-box;
                    }
                    
                    
                    @-webkit-keyframes fadeinout {
                    0%,100% { opacity: 1; }
                    50% { opacity: 0; }
                    }
                    
                    @keyframes fadeinout {
                    0%,100% { opacity: 1; }
                    50% { opacity: 0; }
                    }
                    
                }
            }
        }


    }
    .app{
        position: relative;
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet{
            overflow-y: scroll;
        }
    }
}

.barra{
    position: relative;

    .usuario-barra{
        display: flex;
        padding: 1rem;
        &:hover{
            background-color: black;
        }
    }

    p{
        margin: 0;
        cursor: pointer;
    }


    @include m.tablet{
        display: flex;
        justify-content:  space-between;
        align-items: baseline;
    }

    .boton{
        @include m.boton(v.$negro, v.$blanco);
        margin: 0;
        padding-bottom: 0;
    }
}

.acciones-usuario{
    background-color: rgba(255,255,255,0.8);
    max-width:50%;
    border-radius: 1rem;
    border-top-left-radius: 0;
    display: none;
    position: absolute;
    z-index: 99;

    p{
        padding: 0 2rem;
        font-size: 1.6rem;
        color: v.$negro;
        cursor: pointer;
        user-select: none;

        &:first-of-type{
            margin-top: 0;
        }
        &:hover{
            background-color: v.$azul;
            color: v.$blanco;
        }
    }

    &.mostrar{
        display: block;
    }
}


#overlay {
    position: fixed;
    display: none;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    background: rgba(0, 0, 0, 0.35);
  }
  

.barra-admin{
    margin-bottom: v.$separacion;
    margin-right: 1rem;

    a{
        text-decoration: none;
    }

    @include m.tablet{
        @include m.grid(2, 2fr);
        row-gap: 1rem;
        column-gap: 2rem;
    }

    .boton{
        padding: 1rem;
        margin: 0 0 1rem 0;
        &.activo{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }

}

.volver{
    &:hover{
        text-decoration: none;
    }
}

ion-icon {
    width: 2rem;
    height: 2rem;
    vertical-align: middle;
}