html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 1rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.contenedor-app {
  position: relative;
}
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 22rem;
  background-image: url("../img/1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .imagen .datos {
  position: absolute;
  bottom: 0;
  width: 80%;
  left: 20%;
}
.contenedor-app .imagen .datos .horarios,
.contenedor-app .imagen .datos .direccion {
  width: 60%;
  text-align: center;
  margin-left: 3rem;
}
.contenedor-app .imagen .datos .redes {
  display: flex;
  justify-content: space-evenly;
  bottom: 1rem;
  left: 10%;
  width: 70%;
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0.2);
}
.contenedor-app .imagen .datos .redes img {
  width: 5rem;
  height: 5rem;
}
.contenedor-app .imagen .datos .redes img:hover {
  animation: fadeinout 1.5s ease-in-out infinite;
  border: 3px solid rgba(0, 0, 0, 0.6);
  border-radius: 1rem;
  box-sizing: border-box;
}
@-webkit-keyframes fadeinout {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
@keyframes fadeinout {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
.contenedor-app .app {
  position: relative;
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow-y: scroll;
  }
}

.barra {
  position: relative;
}
.barra .usuario-barra {
  display: flex;
  padding: 1rem;
}
.barra .usuario-barra:hover {
  background-color: black;
}
.barra p {
  margin: 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
  padding-bottom: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

.acciones-usuario {
  background-color: rgba(255, 255, 255, 0.8);
  max-width: 50%;
  border-radius: 1rem;
  border-top-left-radius: 0;
  display: none;
  position: absolute;
  z-index: 99;
}
.acciones-usuario p {
  padding: 0 2rem;
  font-size: 1.6rem;
  color: #1a1b15;
  cursor: pointer;
  user-select: none;
}
.acciones-usuario p:first-of-type {
  margin-top: 0;
}
.acciones-usuario p:hover {
  background-color: #0da6f3;
  color: #FFFFFF;
}
.acciones-usuario.mostrar {
  display: block;
}

#overlay {
  position: fixed;
  display: none;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  background: rgba(0, 0, 0, 0.35);
}

.barra-admin {
  margin-bottom: 5rem;
  margin-right: 1rem;
}
.barra-admin a {
  text-decoration: none;
}
@media (min-width: 768px) {
  .barra-admin {
    display: grid;
    gap: 2fr;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 1rem;
    column-gap: 2rem;
  }
}
.barra-admin .boton {
  padding: 1rem;
  margin: 0 0 1rem 0;
}
.barra-admin .boton.activo {
  background-color: #FFFFFF;
  color: #0da6f3;
}

.volver:hover {
  text-decoration: none;
}

ion-icon {
  width: 2rem;
  height: 2rem;
  vertical-align: middle;
}

.seccion {
  padding: 5rem 0;
  display: none;
  margin: 0 1.5rem;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  transition: transform 500ms ease, background-color 400ms ease;
  text-align: center;
  cursor: pointer;
  user-select: none;
}
.listado-servicios .servicio .nombre-servicio {
  word-wrap: break-word;
  hyphens: auto;
  font-weight: 700;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .listado-servicios .servicio:hover {
    transform: scale(1.08);
  }
}
.listado-servicios .servicio button {
  color: #FFFFFF;
  margin: 0 0 1rem 0;
  padding: 0.2rem;
  display: inline;
  background-color: darkviolet;
  border-style: dashed;
  border-width: thin;
  border-color: #FFFFFF;
}
.listado-servicios .servicio button:hover {
  animation: anim 1s linear infinite;
}
@keyframes anim {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(5deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(-5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.listado-servicios .demora-servicio {
  word-wrap: break-word;
  hyphens: auto;
  font-size: 1rem;
  font-style: italic;
}
.listado-servicios .mapeo-demora {
  display: none;
}
.listado-servicios p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.listado-servicios .precio-contenedor .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.listado-servicios .precio-contenedor .precio-servicio.oferta {
  text-decoration: line-through;
  text-decoration-color: black;
  text-decoration-thickness: 0.4rem;
}
.listado-servicios .precio-contenedor .precio-descuento {
  margin: auto;
  font-weight: 700;
  color: #FFFFFF;
  border-radius: 50%;
  width: 5.5rem;
  height: 5.5rem;
  font-size: 2rem;
  text-align: center;
  user-select: none;
  color: coral;
  background-color: black;
  padding-top: 1.2rem;
  padding-right: 0.2rem;
  position: relative;
  z-index: 0;
}
.listado-servicios .precio-contenedor .precio-descuento .descuento {
  color: #FFFFFF;
  background-color: #cb0000;
  border-radius: 1rem;
  padding: 0.5rem;
  position: absolute;
  font-size: 1rem;
  transform: rotate(30deg);
  bottom: 40%;
  left: 70%;
}

.listado-servicios > * {
  overflow: hidden;
}

.imagenes-servicio {
  position: absolute;
}

.fotos-servicio {
  padding: 1rem;
  display: grid;
  gap: 2fr;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  align-items: center;
  justify-items: center;
}
@media (min-width: 768px) {
  .fotos-servicio {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(3, 1fr);
  }
}
.fotos-servicio .contenedor-img {
  background-color: #FFFFFF;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
}
.fotos-servicio .contenedor-img form {
  margin: 0 auto;
  width: 40%;
}
.fotos-servicio .contenedor-img form .boton-eliminar {
  width: 100%;
  margin: 0;
  padding: 1rem;
}
.fotos-servicio .subir-imagen {
  width: 60%;
  cursor: pointer;
}

.contenido-resumen {
  display: grid;
  grid-template-columns: 3rem;
  padding-right: 1rem;
  gap: 1rem;
}
.contenido-resumen .textoCita {
  text-align: center;
  grid-column: 1;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  background-color: violet;
  line-height: 3rem;
  color: black;
}
.contenido-resumen .textoServicios {
  text-align: center;
  grid-column: 1;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  background-color: violet;
  color: black;
  line-height: 3rem;
}
.contenido-resumen p {
  grid-column: 2;
  margin-bottom: 1rem;
}
.contenido-resumen .boton {
  grid-column: 1/3;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}
.contenido-resumen .porcentaje {
  color: #FFFFFF;
  background-color: #cb0000;
  border-radius: 1rem;
  padding: 0.5rem;
  font-size: 1rem;
}

.contenedor-servicio {
  padding: 1rem 0;
  border-bottom: 1px solid #e1e1e1;
}
.contenedor-servicio .oferta {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}
.contenedor-servicio .oferta .primero {
  text-decoration: line-through;
  text-decoration-color: #0da6f3;
  text-decoration-thickness: 0.4rem;
  color: #FFFFFF;
  font-weight: 400;
}
.contenedor-servicio .oferta .segundo {
  color: #FFFFFF;
  text-decoration: none;
}
.contenedor-servicio .oferta .tercero {
  color: #FFFFFF;
  background-color: #cb0000;
  border-radius: 1rem;
  padding: 0.5rem;
  font-size: 1rem;
}
.contenedor-servicio .sin-oferta span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #0da6f3;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}

.barra-servicios-admin {
  width: 40%;
  margin: 0 auto;
}
.barra-servicios-admin a {
  text-decoration: none;
}
.barra-servicios-admin .boton {
  margin: 1rem;
}

.servicios .acciones {
  margin: 0;
}
.servicios .acciones a:hover {
  text-decoration: none;
}
.servicios .acciones .accion-proyecto {
  margin: 1rem;
  position: relative;
}
.servicios .acciones .accion-proyecto .texto-alerta {
  display: none;
}
.servicios .acciones .accion-proyecto:hover .texto-alerta {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.7);
  display: block;
  color: #1a1b15;
  border-radius: 1rem;
  padding: 0.4rem;
}
.servicios .acciones .accion-proyecto:hover .texto-alerta {
  bottom: 90%;
}
.servicios .boton,
.servicios .boton-foto,
.servicios .boton-eliminar {
  margin: 0;
}

table.usuarios {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  text-align: center;
}
table.usuarios .boton,
table.usuarios .boton-eliminar {
  width: fit-content;
  margin: 1rem auto;
  padding: 0;
}
table.usuarios thead {
  background-color: #1a1b15;
}
table.usuarios thead th {
  color: #FFFFFF;
  padding: 2rem;
  border: 1px solid #e1e1e1;
}
table.usuarios tbody th {
  border: 1px solid #e1e1e1;
}

.acciones-cita {
  display: flex;
  justify-content: space-around;
}
.acciones-cita a {
  all: unset;
}
.acciones-cita .boton {
  background-color: #0da6f3;
  font-weight: 700;
  font-size: 2rem;
}
.acciones-cita .boton,
.acciones-cita .boton-eliminar {
  margin: 1rem;
  padding: 1rem;
}

a.boton {
  text-decoration: none;
}

.contenedor-datos-sucursal .sucursal-campo {
  background-color: antiquewhite;
  border-radius: 1rem;
  color: #1a1b15;
  padding: 0.5rem;
  margin-left: 2rem;
  width: 80%;
}
.contenedor-datos-sucursal .boton {
  width: 30%;
  margin: 0 auto;
  text-decoration: none;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
  margin-right: 1rem;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input,
.campo select {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled,
.campo select:disabled {
  background-color: #6d7158;
  color: #FFFFFF;
  cursor: not-allowed;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
