@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal-app {
    position: absolute;
    background-color: rgb( 0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .mis-citas-modal{
        width: 90%;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        border-radius: 1rem;
        display: grid;

        .x{
            color: v.$negro;
            text-align: center;
            cursor: pointer;
            margin-bottom: 0;
            padding-bottom: 0;
            width: 5rem;
            margin-left: auto;
        }

        h3{
            color: v.$azul;
        }

        .contenedor-importante{
            padding-left: 1rem;
            display: inline-block;
            text-align: right;
            button{
                margin: 1rem 1rem 0 1rem;
            }
            border: solid;
            border-color: #FF9900;
            border-radius: 1rem;
            .importante{
                margin-top: 0;
                text-align: left;
                color: v.$negro;
                span{
                    color: #FF9900;
                }
            }
        }

        .contenedor-aviso{
            padding-left: 1rem;
            @extend .contenedor-importante;
            margin-top: 1rem;
            border-color: #336699;
            .importante span{
                color: #336699;
            }
        }

        .contenedor-mis-citas{
            .no-hay{
                color: v.$negro;
                text-align: center;
            }
            
        }


        &.animar {
            position: relative;
            overflow-y: auto;
            top: 5rem;
            max-height: 100%;
            transform: translateY(-7%);
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }

        .cerrar-modal {
            @include m.boton(v.$negro, v.$blanco);
        }

    }

    .formulario-modal{
        width: 90%;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        border-radius: 1rem;

        .campo{
            margin: 0;
            margin-bottom: 1rem;
            label{
                color: v.$negro;
            }
        }

        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
            color: v.$negro;
        }
        input[type="text"],
        input[type="password"]{
            border: 1px solid v.$gris;
            border-radius: .5rem;
        }
        .opciones {
            display: block;
            .submit-actualizar,
            .cerrar-modal {
                flex: 0 0 5rem;
                margin-top: 1rem;
            }
            .cerrar-modal {
                @include m.boton(v.$negro, v.$blanco);
            }
            .submit-actualizar {
                @include m.boton(v.$azul, v.$blanco);
            }
            @include m.tablet{
                display: flex;
                justify-content: space-between;
            }
        }


        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }
    }
}

.contenedor-cita{
    background-color: v.$negro;
    margin-bottom: 2rem;
    border-radius: 1rem;

    .num-cita{
        padding: 0.5rem 1rem 0 1rem;
        
        background-color: v.$blanco;
        p{
            color: v.$azul;
            background-color: v.$negro;
            border-top-left-radius: 1rem;
            border-top-right-radius: 1rem;
            width: 30%;
        }
    }

    p{
        padding: 1rem 0;
        margin: 0;
    }
    .contenedor-servicio{
        border: none;

        background-color: violet;
        p{
            padding-top: 0;
            padding-bottom: 0;
            color: v.$negro;
        }
    }
    .info-cita{
        padding: 1rem;
        p{
            padding-top: 0;
            padding-bottom: 0;
            color: v.$blanco;
        }
        

    }
}

.contenedor-cita-modal{
    @extend .contenedor-cita;
    .contenedor-servicio,
    .num-cita p{
        padding-left: 1rem;
    }
}