@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{
    display: grid;
    gap: 2rem;
    @include m.desktop{
        @include m.grid(2, 2rem);
    }

    .servicio{
        background-color: v.$blanco;
        padding: 2rem;
        border-radius: 1rem;
        transition: transform 500ms ease, background-color 400ms ease;
        text-align: center;
        cursor: pointer;
        user-select:none;
        .nombre-servicio{
            word-wrap: break-word;
            hyphens: auto;
            font-weight: v.$bold;
        }

        &.seleccionado{
            background-color: v.$azul;
            p{
                color: v.$blanco;
            }
        }

        @include m.tablet{
            &:hover{
                transform: scale(1.08);
            }
        }

        button{
            color: v.$blanco;
            margin: 0 0 1rem 0;
            padding: 0.2rem;
            display: inline;
            background-color: darkviolet;
            border-style: dashed;
            border-width: thin;
            border-color: v.$blanco;
            
            &:hover{
                animation: anim 1s linear infinite;
            }
            @keyframes anim {
                0% {transform: rotate(0deg);}
                25% {transform: rotate(5deg);}
                50% {transform: rotate(0deg);}
                75% {transform: rotate(-5deg);}
                100% {transform: rotate(0deg);}   
              }
        }

    }

    .demora-servicio{
        word-wrap: break-word;
        hyphens: auto;
        font-size: 1rem;
        font-style: italic;
    }
    .mapeo-demora{
        display: none;
    }

    p{
        color: v.$negro;
        margin: 0 0 calc(v.$separacion / 4) 0;
    }
    .precio-contenedor{
        
        .precio-servicio{
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
            &.oferta{
                text-decoration: line-through;
                text-decoration-color: black;
                text-decoration-thickness: 0.4rem;
            }
        }


        
        .precio-descuento{
            margin: auto;
            font-weight: 700;
            color: v.$blanco;
            border-radius: 50%;
            width: 5.5rem;
            height: 5.5rem;
            font-size: 2rem;
            text-align: center;
            user-select:none;
            color: coral;
            background-color: black;
            padding-top: 1.2rem;
            padding-right: 0.2rem;
            position: relative;
            z-index: 0;

            .descuento{
                color: v.$blanco;
                background-color: v.$rojo;
                border-radius: 1rem;
                padding: 0.5rem;
                position: absolute;
                font-size: 1rem;
                transform: rotate(30deg);
                bottom: 40%;
                left: 70%;
            }
        }
    }

}
.listado-servicios > * {
    overflow: hidden;
} 

.imagenes-servicio{
    position: absolute;
}

.fotos-servicio{
    padding: 1rem;
    @include m.grid(2, 2fr);
    gap: 2rem;
    align-items: center;
    justify-items: center;
    @include m.tablet{
        @include m.grid(3, 1fr);
    }

    .contenedor-img{
        background-color: v.$blanco;
        border-bottom-left-radius: 2rem;
        border-bottom-right-radius: 2rem;
        form{
            margin: 0 auto;
            width: 40%;
            
            .boton-eliminar{
                width: 100%;
                margin: 0;
                padding: 1rem;
            }
        }
    }

    .subir-imagen{
        width: 60%;
        cursor: pointer;
    }
}

